<template>
  <div>
    <!-- 搜索结果页-头部导航 -->
    <div class="search-result-container">
      <!-- 点击实现后退效果 -->
      <van-nav-bar title="搜索结果" left-arrow @click-left="$router.go(-1)" fixed />
    </div>

    <!-- 搜索结果列表 -->
    <div>
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了"
        @load="onLoad" offset="50">
        <!-- 一条文章单元格 -->
        <van-cell v-for="artObj in articleList" :key="artObj.art_id"
          @click="goDetailFn(artObj.art_id)">
          <!-- 标题区域的插槽 -->
          <template #title>
            <div class="title-box">
              <!-- 标题 -->
              <span>{{ artObj.title }}</span>
              <!-- 单图 -->
              <img class="thumb" :src="artObj.cover.images[0]"
                v-if="artObj.cover.type === 1" />
            </div>
            <!-- 三图 -->
            <div class="thumb-box" v-if="artObj.cover.type > 1">
              <img :src="item" class="thumb"
                v-for="(item, index) in artObj.cover.images" :key="index" />
            </div>
          </template>
          <!-- label 区域的插槽 -->
          <template #label>
            <div class="label-box">
              <div>
                <span>{{ artObj.aut_name }}</span>
                <span>{{ artObj.comm_count }}评论</span>
                <span>{{ formatTime(artObj.pubdate) }}</span>
              </div>
            </div>
          </template>
        </van-cell>
      </van-list>
    </div>

  </div>
</template>

<script>
import { searchResult } from '@/api/index'
import { timeAgo } from '@/utils/date.js'
export default {
  name: 'SearchResult',
  data () {
    return {
      page: 1, // 页码
      pagNum: 10, // 每页数量
      articleList: [],
      loading: false,
      finished: false
    }
  },
  created () {
    this.getSearchResult()
  },
  methods: {
    name: 'SearchResult',
    formatTime: timeAgo,
    async getSearchResult () {
      const res = await searchResult(this.$route.params.kw, this.page, this.pagNum)
      //   console.log(res)
      if (res.data.data.results.length === 0) {
        this.finished = true
        return
      }
      const newArr = res.data.data.results
      this.articleList = [...this.articleList, ...newArr]
      this.loading = false
    },
    onLoad () {
      if (this.articleList.length === 0) return
      this.page = this.page + 1
      this.getSearchResult()
    },
    // 跳转文章详情界面
    goDetailFn (artId) {
      this.$router.push({
        path: '/article_detail',
        query: { aid: artId }
      })
    }

  }
}

</script>

<style lang="less" scoped>
.search-result-container {
  padding-top: 46px;
}

/* 标题样式 */
.title-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

/* label描述样式 */
.label-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* 文章信息span */
.label-box span {
  margin: 0 3px;
  &:first-child {
    margin-left: 0;
  }
}
/* 图片的样式, 矩形黄金比例：0.618 */
.thumb {
  width: 113px;
  height: 70px;
  background-color: #f8f8f8;
  object-fit: cover;
}

/* 三图, 图片容器 */
.thumb-box {
  display: flex;
  justify-content: space-between;
}
</style>
